body {
  background-color: black;
  font-family: "Red Hat Display", sans-serif;
  font-size: 1.2rem;
  overflow-x: hidden;
  margin: 0;
 }

canvas {
  position: fixed;
  top: 0;
  left: 0;
}

/* Navbar styling */

header {
  display: flex;
  justify-content: center;
  box-shadow: 0.5rem 0.5rem 0.5rem rgba( 0, 0, 0, 0.8);
  z-index: 101;
  background-color: rgba( 0, 0, 0, 0.8);
  margin-left: 0;
  width: 70vw;
  margin-right: 0;
  position: sticky;
  top: 0;
  left: 0;
  padding-top: 1rem;
  padding-left: 15%;
  padding-right: 15%;
  height: 5%;
}

nav {
  width: 50%;
}

nav ul {
  display: flex;
  flex-direction: row;
  text-align: center;
}

li {
  list-style-type: none;
  width: 33%;
}

a, a:hover, a:visited, a:active{
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.28rem;
}

a:hover {
  text-shadow: 0 0 0.2rem white;
}

li a {
  position: relative;
  display: inline-block;
}

li a::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 0.1rem;
  display: block;
  background-color: white;
  transition: width 0.2s ease-in-out;
}

li a:hover::after {
  width: 96%;   
}

/* Main content */

main {
  top: 2vw;
}

main, footer {
  width: 60vw;
  position: relative;
  z-index: 100;
  margin-left: 20%;
  margin-right: 20%;
  color: white;
}

#hero, #about-me, #projects, #connect-with-me {
  margin-top: 5%;
  margin-bottom: 12%;
  background-color: rgba( 0, 0, 0, 0.5);
  border-radius: 1rem;
  padding: 3rem;
}

#hero {
   padding-top: 25%;
   padding-bottom: 25%;
   padding-left: 6%;
}

h1 {
  font-size: 4rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 2.5rem;
}

#about-me, #projects, #connect-with-me {
  text-align: center;
}

footer p {
  margin-top: 10%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  text-align: center;
}

#connect-with-me ul {
  display: flex;
  flex-direction: row;
}